body {
  height: 100vh;
  font-family: "ubunturegular", sans-serif;
  font-size: var(--16px);
  line-height: 1.6;
  letter-spacing: 0.12rem;
  color: var(--main-text-color);
  background-color: var(--main-bg-color);
}

body::-webkit-scrollbar {
    width: 10px;
}

body::-webkit-scrollbar-track {
    background: var(--hover-bg-color);
    border-radius: 20px;
}

body::-webkit-scrollbar-thumb {
    background: var(--secondary-text-color);
    border-radius: 20px;
}

.container {
    display: flex;
}

.aside {
    display: flex;
    flex-direction: column;
    gap: 60px;
    width: 260px;
    min-height: 100vh;
    padding: 20px;
    background-color: var(--secondary-bg-color);
    transition: transform 300ms ease;
}

.header-and-main-wrapper {
    position: relative;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

.header {
    display: none;
    justify-content: flex-end;
    align-items: center;
    position: sticky;
    top: 0;
    padding: 26px;
    background-color: transparent;
}

.main {
    display: flex;
    flex-direction: column;
    min-height: calc(100vh - 76px);
    width: 800px;
    padding: 20px;
    margin: 0 auto;
}

.main-title-and-buttons-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.main-title {
    font-family: "ubuntubold", sans-serif;
    font-size: var(--21px);
    color: var(--secondary-text-color);
}

.buttons-wrapper {
    display: flex;
    align-items: center;
    gap: 6px;
}

.svg {
    display: block;
    width: 24px;
    height: 24px;
    background-color: #2A2D34;
    border-radius: 50%;
    fill: var(--secondary-text-color);
}

.add-task-button, .pages-list-button, .project-list-item-button  {
    display: flex;
    align-items: center;
    gap: 12px;
    width: 100%;
    padding: 6px 12px;
    border-radius: 12px;
}

.add-task-button {
    font-family: "ubuntubold", sans-serif;
    color: var(--secondary-text-color);
    margin-bottom: 16px;
}

.pages-list, .projects-list {
    display: flex;
    flex-direction: column;
    gap: 16px;
}

.project-add-button {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row-reverse;
    width: 100%;
    font-size: var(--18px);
    padding: 6px 0;
    margin-bottom: 20px;
}

.header-buttons-wrapper {
    display: flex;
    align-items: center;
    gap: 12px;
}

.main-page-title {
    font-family: "ubuntubold", sans-serif;
    font-size: var(--48px);
    margin-bottom: 20px;
}

.main-add-task-btn {
    font-family: "ubuntubold", sans-serif;
    font-size: var(--21px);
    display: flex;
    align-items: center;
    gap: 8px;
    color: var(--secondary-text-color);
    margin-bottom: 40px;
}

.active-page-button {
    background-color: var(--hover-bg-color);
}

.close-aside {
    position: absolute;
    transform: translateX(-100%);
}

.open-aside {
    position: absolute;
    top: 0;
    left: 0;
    padding: 22px;
}

.add-task-dialog {
    margin: auto;
    border-radius: 20px;
    width: 800px;
    padding: 40px;
    background-color: var(--secondary-bg-color);
    color: var(--main-text-color);
    overflow: visible;
}

.add-task-dialog::backdrop {
    background-color: rgba(0, 0, 0, 0.4); 
}

.add-task-form {
    display: flex;
    flex-direction: column;
}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
  
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

.task-name-input {
    width: 100%;
    font-size: var(--24px);
    margin-bottom: 20px;
    color: var(--secondary-text-color);
}

.task-description-textarea {
    width: 100%;
    height: 60px;
    font-size: var(--18px);
    margin-bottom: 32px;
    border: none;
    outline: none;
    resize: none
}

.task-description-textarea::-webkit-scrollbar {
    width: 10px;
}

.task-description-textarea::-webkit-scrollbar-track {
    background: var(--hover-bg-color);
    border-radius: 20px;
}

.task-description-textarea::-webkit-scrollbar-thumb {
    background: var(--secondary-text-color);
    border-radius: 20px;
}

.settings-list {
    display: flex;
    gap: 20px;
    margin-bottom: 40px;
}

.settings-list-button {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 12px;
    padding: 8px 12px;
    border: 1px solid var(--main-text-color);
    border-radius: 6px;
}

.datepicker-toggle {
    position: relative;
    padding: 8px 12px;
    border: 1px solid var(--main-text-color);
    border-radius: 6px;
}

.datepicker-toggle-button {
    display: flex;
    gap: 12px;
    justify-content: center;
    align-items: center;
}

.datepicker-input {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
}

.datepicker-input::-webkit-calendar-picker-indicator {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    cursor: pointer;
} 

.add-task-buttons-wrapper, .add-project-buttons-wrapper {
    display: flex;
    justify-content: flex-end;
    gap: 20px;
}

.cancel-task-button, .dialog-add-task-button, .cancel-project-button, .dialog-add-project-button {
    width: 120px;
    padding: 12px 0;
    border-radius: 12px;
    background-color: var(--hover-bg-color);
}

.dialog-add-task-button, .dialog-add-project-button {
    background-color: var(--secondary-text-color);
}

.green {
    border: 2px solid var(--green-color);
    color: var(--green-color);
}

.green-text {
    color: var(--green-color);
}

.green-shadow {
    box-shadow: 0 0 10px var(--green-color);
}

.yellow {
    border: 2px solid var(--yellow-color);
    color: var(--yellow-color);
}

.yellow-text {
    color: var(--yellow-color);
}

.yellow-shadow {
    box-shadow: 0 0 10px var(--yellow-color);
}

.red {
    border: 2px solid var(--red-color);
    color: var(--red-color);
}

.red-text {
    color: var(--red-color);
}

.red-shadow {
    box-shadow: 0 0 10px var(--red-color);
}

.priority-wrapper, .project-wrapper {
    position: relative;
}

.priority-list, .project-list {
    display: flex;
    flex-direction: column;
    gap: 12px;
    position: absolute;
    top: 46px;
    left: 0;
    width: 200px;
    border-radius: 6px;
    padding: 20px;
    background-color: var(--secondary-bg-color);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.priority-list-button, .project-list-button {
    display: flex;
    align-items: center;
    gap: 12px;
    width: 100%;
}

.hide {
    display: none;
}

.add-project-dialog {
    margin: auto;
    border-radius: 20px;
    width: 600px;
    padding: 40px;
    background-color: var(--secondary-bg-color);
    color: var(--main-text-color);
    overflow: visible;
}

.add-project-dialog::backdrop {
    background-color: rgba(0, 0, 0, 0.4); 
}

.add-project-form {
    display: flex;
    flex-direction: column;
}

.button-and-project-name-wrapper {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-bottom: 40px;
}

.project-name-input {
    width: 100%;
    font-size: var(--24px);
    color: var(--secondary-text-color);
}

.color-list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 12px;
    position: absolute;
    top: 72px;
    left: 40px;
    width: 160px;
    border-radius: 6px;
    padding: 6px;
    background-color: var(--secondary-bg-color);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.color-list-item {
    width: 24px;
    height: 24px;
}

.all-tasks-list, .completed-tasks-list, .today-tasks-list, .upcoming-tasks-list, .project-tasks-list {
    display: flex;
    flex-direction: column;
    gap: 40px;
}

.tasks-list-item {
    display: flex;
    align-items: center;
    gap: 20px;
    padding: 12px 20px;
    border-radius: 12px;
    background-color: var(--secondary-bg-color);
}

.title-description-due-date-wrapper {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 12px;
    height: 100%;
}

.task-title {
    font-family: "ubuntubold", sans-serif;
    font-size: var(--21px);
    color: var(--secondary-text-color);
}

.task-description {
    width: 100%;
    max-height: 50px;
    font-size: var(--16px);
    overflow: auto;
}

.task-description::-webkit-scrollbar {
    width: 10px;
}

.task-description::-webkit-scrollbar-track {
    background: var(--hover-bg-color);
    border-radius: 20px;
}

.task-description::-webkit-scrollbar-thumb {
    background: var(--secondary-text-color);
    border-radius: 20px;
}

.manage-and-project-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    height: 100%;
}

.edit-buttons-wrapper {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 12px;
}

.project-list-item {
    border-radius: 12px;
}

.project-div {
    display: flex;
    align-items: center;
    gap: 16px;
}

.project-svg {
    margin-bottom: 20px;
}

