cal-align: top;\n}\n\nimg, svg {\n\tmax-width: 100%;\n\theight: auto;\n}\n\naddress {\n  font-style: normal;\n}\n\n/* Form */\n\ninput, textarea, button, select {\n\tfont-family: inherit;\n    font-size: inherit;\n    letter-spacing: inherit;\n    color: inherit;\n    background-color: transparent;\n}\n\ninput::-ms-clear {\n\tdisplay: none;\n}\n\nbutton, input[type=\"submit\"] {\n    display: inline-block;\n    box-shadow: none;\n    background-color: transparent;\n    background: none;\n    cursor: pointer;\n}\n\ninput:focus, input:active,\nbutton:focus, button:active {\n    outline: none;\n}\n\nbutton::-moz-focus-inner {\n\tpadding: 0;\n\tborder: 0;\n}\n\nlabel {\n\tcursor: pointer;\n}\n\nlegend {\n\tdisplay: block;\n}\n\n/* My styles */\n\n@font-face {\n    font-family: 'ubuntubold';\n    src: url('./fonts/ubuntu-bold-webfont.woff2') format('woff2'),\n         url('./fonts/ubuntu-bold-webfont.woff') format('woff');\n    font-weight: normal;\n    font-style: normal;\n}\n\n@font-face {\n    font-family: 'ubunturegular';\n    src: url('./fonts/ubuntu-regular-webfont.woff2') format('woff2'),\n         url('./fonts/ubuntu-regular-webfont.woff') format('woff');\n    font-weight: normal;\n    font-style: normal;\n\n}\n\nbody {\n  height: 100vh;\n  font-family: \"ubunturegular\", sans-serif;\n  font-size: var(--16px);\n  line-height: 1.6;\n  letter-spacing: 0.12rem;\n  color: var(--main-text-color);\n  background-color: var(--main-bg-color);\n}\n\nbody::-webkit-scrollbar {\n    width: 10px;\n}\n\nbody::-webkit-scrollbar-track {\n    background: var(--hover-bg-color);\n    border-radius: 20px;\n}\n\nbody::-webkit-scrollbar-thumb {\n    background: var(--secondary-text-color);\n    border-radius: 20px;\n}\n\n.container {\n    display: flex;\n}\n\n.aside {\n    display: flex;\n    flex-direction: column;\n    gap: 60px;\n    width: 260px;\n    min-height: 100vh;\n    padding: 20px;\n    background-color: var(--secondary-bg-color);\n    transition: transform 300ms ease;\n}\n\n.header-and-main-wrapper {\n    position: relative;\n    flex-grow: 1;\n    display: flex;\n    flex-direction: column;\n}\n\n.header {\n    display: none;\n    justify-content: flex-end;\n    align-items: center;\n    position: sticky;\n    top: 0;\n    padding: 26px;\n    background-color: transparent;\n}\n\n.main {\n    display: flex;\n    flex-direction: column;\n    min-height: calc(100vh - 76px);\n    width: 800px;\n    padding: 20px;\n    margin: 0 auto;\n}\n\n.main-title-and-buttons-wrapper {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n}\n\n.main-title {\n    font-family: \"ubuntubold\", sans-serif;\n    font-size: var(--21px);\n    color: var(--secondary-text-color);\n}\n\n.buttons-wrapper {\n    display: flex;\n    align-items: center;\n    gap: 6px;\n}\n\n.svg {\n    display: block;\n    width: 24px;\n    height: 24px;\n    background-color: #2A2D34;\n    border-radius: 50%;\n    fill: var(--secondary-text-color);\n}\n\n.add-task-button, .pages-list-button, .project-list-item-button  {\n    display: flex;\n    align-items: center;\n    gap: 12px;\n    width: 100%;\n    padding: 6px 12px;\n    border-radius: 12px;\n}\n\n.add-task-button {\n    font-family: \"ubuntubold\", sans-serif;\n    color: var(--secondary-text-color);\n    margin-bottom: 16px;\n}\n\n.pages-list, .projects-list {\n    display: flex;\n    flex-direction: column;\n    gap: 16px;\n}\n\n.project-add-button {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    flex-direction: row-reverse;\n    width: 100%;\n    font-size: var(--18px);\n    padding: 6px 0;\n    margin-bottom: 20px;\n}\n\n.header-buttons-wrapper {\n    display: flex;\n    align-items: center;\n    gap: 12px;\n}\n\n.main-page-title {\n    font-family: \"ubuntubold\", sans-serif;\n    font-size: var(--48px);\n    margin-bottom: 20px;\n}\n\n.main-add-task-btn {\n    font-family: \"ubuntubold\", sans-serif;\n    font-size: var(--21px);\n    display: flex;\n    align-items: center;\n    gap: 8px;\n    color: var(--secondary-text-color);\n    margin-bottom: 40px;\n}\n\n.active-page-button {\n    background-color: var(--hover-bg-color);\n}\n\n.close-aside {\n    position: absolute;\n    transform: translateX(-100%);\n}\n\n.open-aside {\n    position: absolute;\n    top: 0;\n    left: 0;\n    padding: 22px;\n}\n\n.add-task-dialog {\n    margin: auto;\n    border-radius: 20px;\n    width: 800px;\n    padding: 40px;\n    background-color: var(--secondary-bg-color);\n    color: var(--main-text-color);\n    overflow: visible;\n}\n\n.add-task-dialog::backdrop {\n    background-color: rgba(0, 0, 0, 0.4); \n}\n\n.add-task-form {\n    display: flex;\n    flex-direction: column;\n}\n\n.visually-hidden {\n    position: absolute;\n    width: 1px;\n    height: 1px;\n    margin: -1px;\n    border: 0;\n    padding: 0;\n  \n    white-space: nowrap;\n    clip-path: inset(100%);\n    clip: rect(0 0 0 0);\n    overflow: hidden;\n}\n\n.task-name-input {\n    width: 100%;\n    font-size: var(--24px);\n    margin-bottom: 20px;\n    color: var(--secondary-text-color);\n}\n\n.task-description-textarea {\n    width: 100%;\n    height: 60px;\n    font-size: var(--18px);\n    margin-bottom: 32px;\n    border: none;\n    outline: none;\n    resize: none\n}\n\n.task-description-textarea::-webkit-scrollbar {\n    width: 10px;\n}\n\n.task-description-textarea::-webkit-scrollbar-track {\n    background: var(--hover-bg-color);\n    border-radius: 20px;\n}\n\n.task-description-textarea::-webkit-scrollbar-thumb {\n    background: var(--secondary-text-color);\n    border-radius: 20px;\n}\n\n.settings-list {\n    display: flex;\n    gap: 20px;\n    margin-bottom: 40px;\n}\n\n.settings-list-button {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    gap: 12px;\n    padding: 8px 12px;\n    border: 1px solid var(--main-text-color);\n    border-radius: 6px;\n}\n\n.datepicker-toggle {\n    position: relative;\n    padding: 8px 