, 0, 0, 0.5);\n}\n\n.priority-list-button, .project-list-button {\n    display: flex;\n    align-items: center;\n    gap: 12px;\n    width: 100%;\n}\n\n.hide {\n    display: none;\n}\n\n.add-project-dialog {\n    margin: auto;\n    border-radius: 20px;\n    width: 600px;\n    padding: 40px;\n    background-color: var(--secondary-bg-color);\n    color: var(--main-text-color);\n    overflow: visible;\n}\n\n.add-project-dialog::backdrop {\n    background-color: rgba(0, 0, 0, 0.4); \n}\n\n.add-project-form {\n    display: flex;\n    flex-direction: column;\n}\n\n.button-and-project-name-wrapper {\n    display: flex;\n    align-items: center;\n    gap: 12px;\n    margin-bottom: 40px;\n}\n\n.project-name-input {\n    width: 100%;\n    font-size: var(--24px);\n    color: var(--secondary-text-color);\n}\n\n.color-list {\n    display: flex;\n    justify-content: center;\n    flex-wrap: wrap;\n    gap: 12px;\n    position: absolute;\n    top: 72px;\n    left: 40px;\n    width: 160px;\n    border-radius: 6px;\n    padding: 6px;\n    background-color: var(--secondary-bg-color);\n    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);\n}\n\n.color-list-item {\n    width: 24px;\n    height: 24px;\n}\n\n.all-tasks-list, .completed-tasks-list, .today-tasks-list, .upcoming-tasks-list, .project-tasks-list {\n    display: flex;\n    flex-direction: column;\n    gap: 40px;\n}\n\n.tasks-list-item {\n    display: flex;\n    align-items: center;\n    gap: 20px;\n    padding: 12px 20px;\n    border-radius: 12px;\n    background-color: var(--secondary-bg-color);\n}\n\n.title-description-due-date-wrapper {\n    flex-grow: 1;\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n    gap: 12px;\n    height: 100%;\n}\n\n.task-title {\n    font-family: \"ubuntubold\", sans-serif;\n    font-size: var(--21px);\n    color: var(--secondary-text-color);\n}\n\n.task-description {\n    width: 100%;\n    max-height: 50px;\n    font-size: var(--16px);\n    overflow: auto;\n}\n\n.task-description::-webkit-scrollbar {\n    width: 10px;\n}\n\n.task-description::-webkit-scrollbar-track {\n    background: var(--hover-bg-color);\n    border-radius: 20px;\n}\n\n.task-description::-webkit-scrollbar-thumb {\n    background: var(--secondary-text-color);\n    border-radius: 20px;\n}\n\n.manage-and-project-wrapper {\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n    align-items: flex-end;\n    height: 100%;\n}\n\n.edit-buttons-wrapper {\n    display: flex;\n    justify-content: flex-end;\n    align-items: center;\n    gap: 12px;\n}\n\n.project-list-item {\n    border-radius: 12px;\n}\n\n.project-div {\n    display: flex;\n    align-items: center;\n    gap: 16px;\n}\n\n.project-svg {\n    margin-bottom: 20px;\n}\n\n.flex {\n    display: flex;\n}"],"sourceRoot":""}]);
